/* ------------------------------------------------------------------------------
	Global Styles
-------------------------------------------------------------------------------*/
body {
  font-family: "Lato", sans-serif, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #333333;
  background-color: #fff;
  position: relative;
}

html, body {
  height: 100%;
}

a {
  color: #1ABC9C;
}

a:hover {
  color: #000;
}

a:focus, a:active {
  outline: none;
}

a:hover, a:focus {
  text-decoration: none;
  transition: background-color, color, 0.3s;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

blockquote {
  border-left: none;
  padding-left: 0;
  padding-right: 0;
  padding-top: 5px;
}

.title {
  margin-top: 0;
}

/* ------------------------------------------------------------------------------
	Typography
-------------------------------------------------------------------------------*/
p {
  font-size: 16px;
  line-height: 22.4px;
  color: #6c7279;
}

h1 {
  font-size: 65px;
  color: #2d3033;
}

h2 {
  font-size: 40px;
  color: #2d3033;
}

h3 {
  font-size: 28px;
  color: #2d3033;
  font-weight: 300;
}

h4 {
  font-size: 22px;
  color: #2d3033;
  font-weight: 400;
}

h5 {
  font-size: 14px;
  color: #2d3033;
  text-transform: uppercase;
  font-weight: 700;
}

.section {
  padding: 70px 0;
}

/* ------------------------------------------------------------------------------
	Buttons
-------------------------------------------------------------------------------*/
.btn {
  background-color: #1ABC9C;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  border: 0;
  border-radius: 2px;
  display: inline-block;
  text-transform: uppercase;
  border: 1px solid #1ABC9C;
}

.btn:hover, .btn:focus {
  background-color: transparent;
  color: #1ABC9C;
}

.btn-ultra {
  font-family: "Source Sans Pro", sans-serif;
  background: #fff;
  border: 4px solid #fff;
  border-radius: 0;
  box-shadow: none;
  color: #1ABC9C;
  font-weight: 700;
  padding: 18px 45px;
  min-width: 380px;
  font-size: 24px;
  margin: 0 40px;
  -webkit-font-smoothing: antialiased;
  transition: all .25s;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}

.btn-ultra:hover {
  background: transparent;
  border: 4px solid #fff;
  color: #fff;
  padding: 30px 50px;
  margin-top: -12px;
  margin-bottom: -12px;
}

.btn-ultra em {
  font-style: italic;
  font-size: 16px;
  font-weight: 400;
}

.btn-ultra-white {
  background: #1ABC9C;
  border: 4px solid #1ABC9C;
  color: #fff;
}

.btn-ultra-white:hover {
  background: transparent;
  border: 4px solid #1ABC9C;
  color: #1ABC9C;
}

/* ------------------------------------------------------------------------------
	Header
-------------------------------------------------------------------------------*/
#header {
  position: fixed;
  width: 100%;
  z-index: 999;
  background: transparent;
}

#header .header-content {
  margin: 0 auto;
  max-width: 1170px;
  padding: 60px 0;
  width: 100%;
  transition: padding 0.3s;
}

#header .logo {
  float: left;
  font-size: 24px;
  font-weight: 700;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 7px;
}

#header.fixed {
  background-color: rgba(44, 62, 80, 0.93);
}

#header.fixed a {
  color: #fff;
}

#header.fixed a:hover, #header.fixed a:active {
  color: #1ABC9C;
}

#header.fixed .header-content {
  border-bottom: 0;
  padding: 25px 0;
}

#header.fixed .nav-toggle {
  top: 18px;
  color: #000;
}

/* ------------------------------------------------------------------------------
	Banner
-------------------------------------------------------------------------------*/
.banner {
  background-image: url(./img/backs/banner.jpg);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 350px;
  height: 100%;
}

.banner-text {
  padding-top: 45%;
}

.banner-text h1 {
  color: #fff;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 36px;
  font-weight: 700;
  text-transform: uppercase;
}

.banner-text p {
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  margin-top: 30px;
  margin-bottom: 80px;
}

/* navigation ----------------*/
.navigation {
  float: right;
}

.navigation.open {
  opacity: 0.9;
  visibility: visible;
  transition: opacity 0.5s;
}

.navigation li {
  display: inline-block;
}

.navigation a {
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  margin-left: 40px;
  text-transform: uppercase;
}

.navigation a:hover, .navigation a.active {
  color: #1ABC9C;
}

/* nav-toggle ----------------*/
.nav-toggle {
  display: none;
  height: 44px;
  overflow: hidden;
  position: fixed;
  right: 5%;
  text-indent: 100%;
  top: 32px;
  white-space: nowrap;
  width: 44px;
  z-index: 99999;
  transition: all 0.3s;
  background: transparent;
  /* close-nav is for the close to open animation ----------------*/
}

.nav-toggle:before, .nav-toggle:after {
  border-radius: 50%;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  transform: translateZ(0);
  backface-visibility: hidden;
  transition-property: transform;
}

.nav-toggle:before {
  transform: scale(1);
  transition-duration: 0.3s;
  color: #1ABC9C;
}

.nav-toggle:after {
  background-color: transparent;
  transform: scale(0);
  transition-duration: 0s;
  color: #1ABC9C;
}

.nav-toggle span {
  bottom: auto;
  display: inline-block;
  height: 3px;
  left: 50%;
  position: absolute;
  right: auto;
  top: 50%;
  width: 18px;
  z-index: 10;
  transform: translateX(-50%) translateY(-50%);
}

.nav-toggle span:before, .nav-toggle span:after {
  background-color: #fff;
  content: "";
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  transform: translateZ(0);
  backface-visibility: hidden;
  transition: transform 0.3s;
}

.nav-toggle span:before {
  transform: translateY(-6px) rotate(0deg);
}

.nav-toggle span:after {
  transform: translateY(6px) rotate(0deg);
}

.nav-toggle.close-nav:before {
  transform: scale(0);
}

.nav-toggle.close-nav:after {
  transform: scale(1);
}

.nav-toggle.close-nav span {
  background-color: rgba(255, 255, 255, 0);
}

.nav-toggle.close-nav span:before, .nav-toggle.close-nav span:after {
  background-color: #fff;
}

.nav-toggle.close-nav span:before {
  transform: translateY(0) rotate(45deg);
}

.nav-toggle.close-nav span:after {
  transform: translateY(0) rotate(-45deg);
}

/* intro
---------------------------------- */
#intro {
  background-color: #2c3e50;
}

#intro h2 {
  color: #fff;
}

#intro p {
  color: #8D9DAD;
}

/* Media
---------------------------------- */
.media .fa {
  font-size: 38px;
  width: 40px;
  height: 25px;
  line-height: 48px;
  padding: 0 5px;
  text-align: center;
}

.space {
  padding: 20px 0;
}

blockquote {
  padding: 10px 5px;
  margin: 0 0 20px;
  font-size: 17.5px;
}

/* block-lists
---------------------------------- */
.block-list .media-left {
  width: 60px;
}

.block-list p {
  font-size: 14px;
}

.block-list .fa {
  padding: 20px;
  width: 88px;
  height: 88px;
  border-radius: 50%;
  color: #fff;
  background: #1ABC9C;
}

/* features
---------------------------------- */
#features {
  background-color: #e9f0f2;
}

#features p {
  color: #8D9DAD;
}

#features .blank-section {
  height: 800px;
}

@media screen and (max-width: 1024px) {
  #header .header-content {
    width: 90%;
    padding: 40px 0;
  }
  #header.fixed a {
    color: #fff;
  }
  .nav-toggle {
    display: block;
  }
  .navigation {
    position: fixed;
    background-color: #313131;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 99999;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.5s, visibility 0s 0.5s;
  }
  .navigation .primary-nav {
    position: relative;
    top: 45%;
    transform: translateY(-45%);
  }
  .navigation li {
    display: block;
    margin-bottom: 1px;
  }
  .navigation a {
    display: block;
    font-size: 18px;
    margin: 0;
    text-align: center;
    padding: 17px 0;
    color: #fff;
  }
}

@media screen and (max-width: 768px) {
  #header.fixed a {
    color: #fff;
  }
  #header.fixed a.logo {
    color: #000;
  }
}
